body {
  color: #333;
  --bg-color: #f8f9fa;
  --text-color: #333;
  --header-bg: #2a7a29;
  --sidebar-bg: #fff;
  --sidebar-border: #eee;
  --content-bg: #fff;
  --toc-link: #f052a1;
  --toc-active: #5e94fa;
  --code-bg: #88c070;
  --code-header: #f1ffeb;
  --image-border: #000;
  --svg-bg: unset;
  --highlight-bg: #f0f7ff;
  --message-bg: #f8fff0;
  --warn-bg: #fff9f0;
  --error-bg: #fff0f0;
  --link-shadow-col-a: #e7d9d3;
  --link-shadow-col-b: #dfded6;
  --link-shadow-col-c: #b79b9b;
  --pagination-col-a: #f1f8ff;
  --pagination-col-b: #06c;
  --pagination-col-c: #f5f5f5;
  --pagination-col-d: #06c;
  background-color: #f8f9fa;
  flex-direction: column;
  margin: 0 auto;
  padding-top: 60px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, sans-serif;
  line-height: 1.6;
  transition: all .5s;
  display: flex;
}

body.dark-mode {
  --bg-color: #1a1a1a;
  --text-color: #e0e0e0;
  --header-bg: #1a472a;
  --sidebar-bg: #2d2d2d;
  --sidebar-border: #444;
  --content-bg: #252525;
  --toc-link: #ff79c6;
  --toc-active: #66a3fc;
  --code-bg: #50a14f;
  --code-header: #c2f0ae;
  --image-border: #898989;
  --svg-bg: #f5f2f0;
  --highlight-bg: #2d2d35;
  --message-bg: #292c25;
  --warn-bg: #2b2b24;
  --error-bg: #292525;
  --link-shadow-col-a: #a16247;
  --link-shadow-col-b: #040404;
  --link-shadow-col-c: #8f6666;
  --pagination-col-a: #06c;
  --pagination-col-b: #f1f8ff;
  --pagination-col-c: #06c;
  --pagination-col-d: #f5f5f5;
  background-color: var(--bg-color);
  color: var(--text-color);
}

body.dark-mode #sidebar {
  background-color: var(--sidebar-bg);
}

body.dark-mode h1, body.dark-mode h2, body.dark-mode h3, body.dark-mode h4, body.dark-mode h5, body.dark-mode h6 {
  color: #e0e0e0;
  border-bottom: 1px solid #444;
}

body::-webkit-scrollbar {
  width: 16px;
}

body::-webkit-scrollbar-track {
  background: #a3e584cc;
  border: 2px solid #789b78;
  border-radius: 10px;
}

body::-webkit-scrollbar-thumb {
  background: #789b78;
  border: 3px solid #f5f7ed;
  border-radius: 10px;
}

body::-webkit-scrollbar-thumb:hover {
  background: #3a9a39;
}

body {
  scrollbar-width: thin;
  scrollbar-color: #789b78 #f5f7ed;
}

#top-banner {
  z-index: 1000;
  box-sizing: border-box;
  color: #fff;
  background-color: #2a7a29;
  border: 1px solid #ffffff4d;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 20px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

#top-banner h1 {
  color: #fff;
  border-bottom: 0;
  flex-shrink: 0;
  margin: 0;
  padding-bottom: 0;
  font-size: 1.5rem;
}

.top-banner-buttons {
  gap: 4px;
  margin-left: 10px;
  display: flex;
}

input:focus, textarea:focus {
  box-shadow: 0 0 0 1px #5e94fa;
  -webkit-box-shadow: none;
  box-shadow: none;
  outline: 2px solid #5e94fa;
}

#search-container {
  flex: 1;
  justify-content: flex-end;
  align-items: center;
  width: 200px;
  margin-left: auto;
  transition: width .3s;
  display: flex;
  position: relative;
}

#search-container:focus-within {
  width: 90%;
  max-width: none;
  margin-left: 30px;
}

#search-container:focus-within #global-search {
  opacity: 1;
  cursor: text;
  flex: 1;
  width: 100%;
}

#search-container i {
  cursor: pointer;
  z-index: 1;
  color: #ffffffb3;
  position: relative;
  left: 28px;
}

#search-container:focus-within i {
  color: #fff;
}

#global-search {
  color: #fff;
  opacity: .8;
  cursor: pointer;
  background: #ffffff1a;
  border: 1px solid #ffffff4d;
  border-radius: 4px;
  flex: 0;
  width: 150px;
  padding: 8px 12px 8px 35px;
  transition: all .3s;
}

#global-search::placeholder {
  color: #ffffffb3;
}

.icon-btn {
  color: #fff;
  cursor: pointer;
  background: none;
  border: 1px solid #ffffff4d;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  font-size: 1.5em;
  transition: all .2s;
  display: flex;
}

.icon-btn:hover {
  background-color: #ffffff1a;
  transform: translateY(-1px);
}

#sidebar-toggle {
  cursor: pointer;
  background: none;
  border: none;
  flex-direction: column;
  justify-content: space-around;
  width: 30px;
  min-width: 30px;
  height: 30px;
  margin-right: 10px;
  padding: 0;
  display: flex;
}

#sidebar-toggle span {
  background-color: #fff;
  border-radius: 2px;
  width: 100%;
  height: 3px;
  display: block;
}

#app {
  flex-direction: row;
  width: 100%;
  height: calc(100vh - 60px);
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
}

#doc {
  background: var(--content-bg);
  flex-shrink: 0;
  width: 50%;
  min-width: 640px;
  max-width: 1280px;
  min-height: 80vh;
  padding: 0;
  display: flex;
  position: relative;
  left: 0;
}

#doc-overlay {
  z-index: 1;
  background-color: #cbe1b400;
  width: 100%;
  height: 100%;
  display: none;
  position: absolute;
}

#resizer {
  cursor: col-resize;
  z-index: 100;
  background-color: #eff3de;
  width: 5px;
  position: relative;
}

#resizer:hover {
  background-color: #beceb5;
}

#resizer-overlay {
  text-align: center;
  color: #4e8b49;
  opacity: 0;
  z-index: 1;
  background-color: #cbe1b4;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: xx-large;
  font-weight: bold;
  transition: opacity .15s;
  display: none;
  position: relative;
}

#resizer-icon {
  font-size: 1.5em;
}

.resizer-icon-face {
  border: 4px solid #4e8b49;
  border-radius: 8px;
  width: 32px;
  margin: auto;
  padding: 12px;
}

#emu {
  background: #939f93;
  flex: 1;
  position: relative;
  bottom: auto;
}

#sidebar {
  border-right: 1px solid var(--sidebar-border);
  background: var(--sidebar-bg);
  scrollbar-width: thin;
  width: 250px;
  height: calc(100vh - 60px);
  padding-left: 1rem;
  padding-right: 20px;
  transition: transform .1s;
  position: sticky;
  overflow-y: auto;
}

#toc {
  position: sticky;
  top: 0;
}

#toc ul {
  margin-top: 4px;
  padding-left: 1em;
  list-style: none;
}

#toc a {
  color: var(--toc-link);
  padding: 0;
  text-decoration: none;
  display: block;
}

#toc a:hover {
  text-decoration: underline;
}

#toc a.active {
  color: var(--toc-active);
  font-weight: bold;
  text-decoration: underline;
}

#toc li {
  position: relative;
}

#toc ul ul {
  transition: max-height .3s, opacity .3s;
  overflow: hidden;
}

#toc ul ul.collapsed {
  opacity: 0;
  max-height: 0;
}

#toc ul ul:not(.collapsed) {
  opacity: 1;
  max-height: 1000px;
}

.toc-toggle {
  cursor: pointer;
  text-align: center;
  background: none;
  border: none;
  width: 15px;
  padding: 0 10px 0 0;
  position: absolute;
  top: .3em;
  left: -15px;
}

.toc-toggle:before {
  content: "";
  color: var(--toc-link);
  font-family: "Font Awesome 7 Free";
  font-size: 1.2em;
  font-weight: 900;
}

.toc-toggle.collapsed:before {
  content: "";
}

#toc-search {
  z-index: 10;
  background: var(--sidebar-bg);
  border-bottom: 1px solid #eee;
  align-items: center;
  margin-bottom: 0;
  padding: 10px 0;
  display: flex;
  position: sticky;
  top: 0;
}

#toc-search i {
  color: var(--toc-link);
  margin-right: 8px;
  font-size: 14px;
}

#toc-search-input {
  border: 1px solid #ddd;
  border-radius: 4px;
  outline: none;
  flex: 1;
  padding: 6px 8px;
  font-size: 14px;
  transition: border-color .2s;
}

#toc-search-input:focus {
  border-color: var(--toc-link);
}

#toc li.hidden {
  display: none;
}

#toc .no-results {
  text-align: center;
  color: #666;
  padding: 10px;
  font-style: italic;
}

#breadcrumb {
  color: #afafaf;
  padding: .5rem 0;
  font-size: .9rem;
}

#breadcrumb a {
  color: var(--toc-link);
  text-decoration: none;
}

#breadcrumb a:hover {
  text-decoration: underline;
}

#breadcrumb span:last-child {
  color: #333;
  font-weight: 500;
}

#content {
  scrollbar-width: thin;
  flex-direction: column;
  flex: 1;
  align-items: center;
  height: calc(100vh - 80px);
  padding-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  overflow-y: auto;
}

#markdown-content {
  flex: 1;
  transition: opacity .3s;
}

#markdown-content.loading {
  opacity: .5;
  pointer-events: none;
}

#content-and-pagination {
  width: 100%;
  min-width: 80%;
  max-width: 1000px;
  margin: 0 auto;
}

#pagination {
  justify-content: space-between;
  padding-top: 1em;
  display: flex;
}

.pagination-link {
  cursor: pointer;
  background-color: var(--pagination-col-a);
  color: var(--pagination-col-b);
  border: 1px solid #ddd;
  border-radius: 4px;
  margin: 0;
  padding: 4px 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .2s;
  display: inline-block;
  position: relative;
}

.pagination-link:before {
  content: attr(title);
}

.pagination-link:hover {
  border-color: var(--pagination-col-d);
  background-color: var(--pagination-col-c);
}

.pagination-link .full-text {
  display: inline;
}

.pagination-link .short-text {
  display: none;
}

#comments {
  border-top: 1px solid #eee;
  margin-top: 1rem;
}

#comments-title {
  font-size: 1.5em;
  font-weight: bold;
}

#comments-chapter {
  font-size: 1em;
}

.comments-error {
  text-align: center;
  color: #c62828;
  background-color: #ffebee;
  border: 1px solid #ffcdd2;
  border-radius: 6px;
  margin: 20px 0;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, sans-serif;
}

.small-note {
  text-align: center;
  color: gray;
  font-size: 12px;
}

.small-note a, .nav-link {
  color: var(--toc-link);
}

.nav-link-prev {
  float: none;
  color: var(--toc-link);
  text-shadow: 0 0 5px var(--link-shadow-col-a), 0 0 4px var(--link-shadow-col-b), -2px 2px 2px var(--link-shadow-col-c);
  text-decoration: underline;
}

.nav-link-next {
  float: right;
  color: var(--toc-link);
  text-shadow: 0 0 5px var(--link-shadow-col-a), 0 0 4px var(--link-shadow-col-b), -2px 2px 2px var(--link-shadow-col-c);
  text-decoration: underline;
}

.error {
  color: #dc3545;
  background: #dc35450d;
  border: 1px solid #dc35454d;
  border-radius: 4px;
  padding: 1rem;
}

.error-toast {
  z-index: 2000;
  color: #fff;
  background: #dc3545;
  border-radius: 4px;
  max-width: 300px;
  padding: 1rem;
  position: fixed;
  top: 80px;
  right: 20px;
  box-shadow: 0 4px 12px #00000026;
}

#gt_float_wrapper {
  transform: translateY(5px);
  bottom: 0 !important;
  left: unset !important;
  position: absolute !important;
  right: 39px !important;
}

h1, h2, h3, h4, h5, h6 {
  color: #2c3e50;
  border-bottom: 1px solid #eee;
  padding-bottom: .3em;
}

#markdown-content h1 {
  margin-top: 0;
}

#markdown-content ul {
  padding-left: 1.5em;
}

#markdown-content ul ul, #markdown-content ul ul ul, #markdown-content ul ul ul ul {
  padding-left: 1em;
}

table {
  border-collapse: separate;
  border-spacing: 0;
  color: #333;
  text-align: left;
  scrollbar-width: thin;
  background-color: #f5f2f0;
  border: 1px solid #f5f2f0;
  border-radius: 6px;
  width: 100%;
  margin: .5em 0;
  display: table;
  overflow: auto hidden;
}

thead th {
  background: #f1ffeb;
  border: 1px solid #e6e6e6;
}

tr > :first-child {
  border-left: none;
}

tr > :last-child {
  border-right: none;
}

table::-webkit-scrollbar {
  display: none;
}

td, th {
  border: 1px solid #e6e6e6;
  border-bottom: none;
  padding: .225rem .25rem;
  font-size: .875rem;
}

thead {
  background-color: #f1ffeb;
}

tr:first-child th:first-child {
  border-top-left-radius: 6px;
}

tr:first-child th:last-child {
  border-top-right-radius: 6px;
}

tr:last-child td:first-child {
  border-bottom-left-radius: 6px;
}

tr:last-child td:last-child {
  border-bottom-right-radius: 6px;
}

pre {
  scrollbar-width: thin;
  background: #f8f8f8;
  border-radius: 0;
  margin: 0;
  padding: 0;
  font-size: small;
  line-height: normal;
  position: relative;
  overflow: auto;
}

.code-block {
  border-left: 4px solid var(--code-bg);
  border-right: 4px solid var(--code-bg);
  background: var(--code-header);
  border-radius: 5px;
  margin: 0 0 1.5em;
  padding-top: 5px;
}

.code-container {
  display: flex;
  overflow-x: auto;
}

.code-image-container {
  background-color: #f5f2f0;
  justify-content: center;
  align-items: center;
  padding: 8px;
  display: flex;
  overflow: hidden;
}

.code-image-container img {
  object-fit: contain;
  width: auto;
  max-width: 100%;
  height: auto;
  position: relative;
}

.line-numbers {
  color: #6a737d;
  text-align: right;
  user-select: none;
  background-color: #f5f2f0;
  margin: 0;
  padding: calc(1em + 1px) 1em 1em;
}

.line-numbers-code {
  color: #2b91af;
  background: none;
  padding: 0;
}

.code-header {
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 5px;
  display: flex;
}

.code-header-title {
  color: #333;
  margin-right: auto;
  padding: 0 10px;
  font-weight: bold;
}

.code-buttons {
  justify-content: flex-end;
  display: flex;
}

.code-btn {
  color: #fff;
  cursor: pointer;
  background: #2a7a29;
  border: none;
  border-radius: 4px;
  padding: 10px 30px;
  font-size: .85em;
  transition: all .24s ease-in-out;
}

.code-btn:hover {
  background: #328e31;
  transition: all .24s ease-in-out;
  transform: translateY(-.05rem);
  box-shadow: 0 5px 15px #00000026;
}

.code-btn.loading {
  opacity: .7;
  pointer-events: none;
  position: relative;
}

.code-btn.loading:after {
  content: "";
  border: 2px solid #0000;
  border-top-color: #fff;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  margin: -8px 0 0 -8px;
  animation: 1s linear infinite spin;
  position: absolute;
  top: 50%;
  left: 50%;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.spinner {
  border: 4px solid #0000001a;
  border-top-color: #479f1b;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: 1s linear infinite spin;
}

.edit-btn {
  margin-right: 4px;
}

.run-btn {
  background: #5e94fa;
  margin-right: 4px;
}

.run-btn:hover {
  background: #66a3fc;
}

code {
  background: var(--code-bg);
  color: #fff;
  scrollbar-width: thin;
  border-radius: 3px;
  padding: .2em .4em;
  font-family: Consolas, Monaco, Andale Mono, monospace;
}

details {
  color: #2a7a29;
}

details .details-text {
  color: var(--text-color);
  border: 1px solid #d3d3d3;
  border-radius: 6px;
  margin-top: 2px;
  padding: 8px;
  font-size: small;
  line-height: normal;
  box-shadow: inset 0 1px 3px #979797f5;
}

details[open] > summary:first-of-type {
  box-shadow: inset 0 2px 4px #000000f5;
}

details summary {
  color: #fff;
  cursor: pointer;
  user-select: none;
  background-color: #2a7a29;
  border-radius: 4px;
  padding: 2px 20px;
  font-weight: 500;
  transition: background-color .2s;
  display: inline-block;
}

details summary:hover {
  background-color: #328e31;
}

.content-highlight {
  background: var(--highlight-bg);
  border-left: 4px solid #5e94fa;
  border-radius: 6px;
  margin: 1em 0;
  padding: 1em;
}

.content-message {
  background: var(--message-bg);
  border-left: 4px solid #8bc34a;
  border-radius: 6px;
  margin: 1em 0;
  padding: 1em;
}

.content-warn {
  background: var(--warn-bg);
  border-left: 4px solid #ff9800;
  border-radius: 6px;
  margin: 1em 0;
  padding: 1em;
}

.content-error {
  background: var(--error-bg);
  border-left: 4px solid #f44336;
  border-radius: 6px;
  margin: 1em 0;
  padding: 1em;
}

.content-gray {
  background: var(--bg-color);
  border-left: 4px solid #6c757d;
  border-radius: 6px;
  margin: 1em 0;
  padding: 1em;
}

.content-text {
  overflow: hidden;
}

.hljs {
  scrollbar-width: thin;
  background: #f5f2f0;
  padding: 1em;
  display: block;
  overflow-x: auto;
}

.hljs:before {
  content: "";
  z-index: 2;
  opacity: 0;
  pointer-events: none;
  background: linear-gradient(to right, #0000001a 0%, #0000 100%);
  width: 8px;
  transition: opacity .3s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

.hljs.scrolled:before {
  opacity: 1;
}

.title-banner {
  background-color: #88c070;
  width: 100%;
  padding: 0;
  box-shadow: -3px 10px 10px #0003;
}

.logo-and-title {
  justify-content: space-between;
  align-items: center;
  width: 80%;
  margin: 0;
  padding: 20px 0;
  display: flex;
}

.icon-image {
  width: 128px;
}

.logo-image {
  object-fit: contain;
  width: 40%;
  display: block;
}

.title-image {
  object-fit: contain;
  width: 60%;
  display: block;
  position: relative;
}

.banner-image {
  object-fit: contain;
  width: 100%;
  display: block;
}

.diagram-screenshot {
  margin: auto;
}

.diagram-schematic {
  margin: unset;
}

.diagram-image {
  border: 2px solid var(--image-border);
  border-radius: 6px;
  max-width: 100%;
  display: block;
  position: relative;
}

.diagram-svg {
  background-color: var(--svg-bg);
  border: 2px solid var(--image-border);
  border-radius: 6px;
}

.stacked-image-container {
  perspective: 1000px;
  perspective-origin: center;
  padding: 40px 20px 40px 50px;
}

.stacked-image-set {
  width: 320px;
  height: 288px;
  transform-style: preserve-3d;
  transition: transform .6s;
  position: relative;
  transform: rotateX(350deg)rotateY(13deg)rotateZ(2deg);
}

.stacked-image-set:hover {
  transform: rotateX(331deg)rotateY(45deg)rotateZ(10deg)scale(1.05);
}

.stacked-image {
  border-radius: 0;
  width: 100%;
  height: 100%;
  transition: all .6s;
  position: absolute;
  overflow: hidden;
  box-shadow: 0 10px 30px #0000004d;
}

.stacked-image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  image-rendering: pixelated;
  image-rendering: -moz-crisp-edges;
  image-rendering: crisp-edges;
  display: block;
}

.stacked-image:first-child {
  opacity: .8;
  transform: translateZ(-60px);
}

.stacked-image:nth-child(2) {
  opacity: .9;
  transform: translateZ(-30px);
}

.stacked-image:nth-child(3) {
  opacity: 1;
  transform: translateZ(0);
}

.stacked-image:before {
